  from {
    border-bottom: 0 solid transparent; }
  to {
    border-bottom: 1px solid #008C75; } }

.hero {
  height: 100vh;
  margin: 0;
  padding: 0; }

#about-me {
  height: 100vh;
  margin: 0; }
  #about-me .photo {
    border-radius: 50%;
    min-height: 200px;
    min-width: 200px;
    margin-right: 16px;
    background-image: url("/assets/imgs/me3.jpg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat; }
  #about-me h1 {
    margin-bottom: 16px;
    font-size: 2rem;
