* {
  font-family: 'Montserrat', SansSerif; }

*, *:after, *:before {
  box-sizing: border-box; }

body {
  padding-top: 50px;
  margin: 0; }

section {
  margin: 96px 0;
  padding: 0 20vw; }
  section h2 {
    padding-top: 16px;
    margin-bottom: 0; }
  section > div {
    margin-top: 32px; }

p {
  line-height: 1.4rem;
  text-indent: 2rem;
  text-align: justify; }

a, a:hover {
  text-decoration: none;
  color: #006E84; }
  a:hover {
    color: #008C75; }

@media screen and (max-width: 768px) {
  section {
    padding: 0 3vw; }
  #about-me {
    flex-direction: column; } }

.bold {
